<template>
    <div class="container px-3 py-3 pt-md-5 pb-md-4 mx-auto">
	<div class="row">
		<div class="col-sm-5">
<article class="gallery-wrap"> 
<div class="img-big-wrap">
  <div> <a href="#"><img class="img-fluid" :src="domain + '/img/products/' + productDetail.foto"></a></div>
</div> 
<!-- slider-product.// -->
<!-- <div class="img-small-wrap">
  <div class="item-gallery"> <img src="https://s9.postimg.org/tupxkvfj3/image.jpg"> </div>
  <div class="item-gallery"> <img src="https://s9.postimg.org/tupxkvfj3/image.jpg"> </div>
  <div class="item-gallery"> <img src="https://s9.postimg.org/tupxkvfj3/image.jpg"> </div>
  <div class="item-gallery"> <img src="https://s9.postimg.org/tupxkvfj3/image.jpg"> </div>
</div> -->
<!-- slider-nav.// -->
</article> <!-- gallery-wrap .end// -->
		</div>
		<div class="col-sm-7">
	<h3 class="title mb-3">{{productDetail.name}}</h3>

<p class="price-detail-wrap"> 
	<span class="price h3 text-warning"> 
		<span class="num">{{productDetail.price | currency}}</span>
	</span>
</p> <!-- price-detail-wrap .// -->
<dl class="item-property">
  <dt>Description</dt>
  <dd><p><pre>{{productDetail.description}}</pre></p></dd>
</dl>

	<hr>
	<a href="#" class="btn btn-lg btn-primary text-uppercase"> Buy now </a>
	<a href="#" class="btn btn-lg btn-outline-primary text-uppercase"> <i class="fas fa-shopping-cart"></i> Continue Shopping </a>

		</div> <!-- col.// -->
	</div> <!-- row.// -->
    </div>
</template>

<script>
export default {
    name: 'ProductDetail',
    props: ['id'],
    created () {
        this.$store.dispatch('productDetail', {id: this.id})
    },
    computed: {
        productDetail () {
            return this.$store.getters.productDetail
        },
        domain () {
            return this.$store.state.domainName
        },
    },
}
</script>

<style scoped>
.gallery-wrap .img-big-wrap img {
    height: auto;
    width: auto;
    display: inline-block;
    cursor: zoom-in;
}

.gallery-wrap .img-small-wrap .item-gallery {
    width: 60px;
    height: 60px;
    border: 1px solid #ddd;
    margin: 7px 2px;
    display: inline-block;
    overflow: hidden;
}

.gallery-wrap .img-small-wrap {
    text-align: center;
}
.gallery-wrap .img-small-wrap img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 4px;
    cursor: zoom-in;
}
</style>




