<template>
<div class="pt-md-4">
 <div class="row">
   <div class="col-md-8">
     <div class="row">
    <div v-for="product in products" :key="product.id" class="col-md-4">
      <div class="card mb-4 shadow-sm">
        <img class="card-img-top" :src="domain + '/img/products/' + product.foto">
        <div class="card-body">
            <h5 class="card-title">{{product.name}}</h5>
          <p class="card-text">{{product.price | currency}}</p>
          <div class="d-flex justify-content-between align-items-center">
            <div class="btn-group">
              <button @click="onView(product.id)" type="button" class="btn btn-sm btn-outline-secondary">View</button>
              <button type="button" @click="addProductToCart(product)" class="btn btn-sm btn-outline-secondary">Buy</button>
            </div>
            <small class="text-muted">Stock: {{product.stock}}</small>
          </div>
        </div>
      </div>
    </div>
     </div>
   </div>
    <div class="col-md-4">
      <CartSide/>
    </div>
 </div>
</div>
</template>

<script>
import CartSide from './product/CartSide'
export default {
    name: 'Welcome',
    components: {
            CartSide
        },
    created () {
        this.$store.dispatch('products')
    },
    computed: {
      products () {
          return this.$store.getters.products
      },
      domain () {
        return this.$store.state.domainName
      },
    },
    methods: {
      onView (id) {
        this.$router.push('product-detail/' + id)
      },
      addProductToCart (product) {
        this.$store.dispatch('addProductToCart', product)
      },
    }
}
</script>




