<template>
    <div class="row">
        <div class="col-12">
            <div class="table-responsive">
                <table class="table table-striped">
                    <thead>
                        <tr>
                            <th scope="col"><small>Product</small></th>
                            <th scope="col" class="text-center"><small>Quantity</small></th>
                            <th scope="col" class="text-right"><small>Price</small></th>
                            <th> </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="cart in carts" :key="cart.id">
                            <td><small>{{cart.name}}</small></td>
                            <td><input class="form-control"  type="text" :value="cart.qty" /></td>
                            <td class="text-right"><small>{{cart.price | currency}}</small></td>
                            <td class="text-right"><button class="btn btn-sm btn-danger"><font-awesome-icon icon="trash" /> </button> </td>
                        </tr>
                        <tr>
                            <td><strong>Total</strong></td>
                            <td colspan="3" class="text-right"><strong>{{total | currency}}</strong></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="col mb-2">
            <div class="row">
                <div class="col-sm-12  col-md-6">
                    
                </div>
                <div class="col-sm-12 col-md-6 text-right">
                    <button class="btn btn-lg btn-block btn-success text-uppercase">Checkout</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CartSide',
    computed: {
        carts () {
            return this.$store.getters.carts
        },
        total () {
            return this.$store.getters.cartTotal
        },
        domain () {
            return this.$store.state.domainName
        },
    }
}
</script>

<style scoped>
.bloc_left_price {
    color: #c01508;
    text-align: center;
    font-weight: bold;
    font-size: 150%;
}
.category_block li:hover {
    background-color: #007bff;
}
.category_block li:hover a {
    color: #ffffff;
}
.category_block li a {
    color: #343a40;
}
.add_to_cart_block .price {
    color: #c01508;
    text-align: center;
    font-weight: bold;
    font-size: 200%;
    margin-bottom: 0;
}
.add_to_cart_block .price_discounted {
    color: #343a40;
    text-align: center;
    text-decoration: line-through;
    font-size: 140%;
}
.product_rassurance {
    padding: 10px;
    margin-top: 15px;
    background: #ffffff;
    border: 1px solid #6c757d;
    color: #6c757d;
}
.product_rassurance .list-inline {
    margin-bottom: 0;
    text-transform: uppercase;
    text-align: center;
}
.product_rassurance .list-inline li:hover {
    color: #343a40;
}
.reviews_product .fa-star {
    color: gold;
}
.pagination {
    margin-top: 20px;
}
footer {
    background: #343a40;
    padding: 40px;
}
footer a {
    color: #f8f9fa!important
}
</style>





