import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
axios.defaults.baseURL = 'https://tutashop.irfanmg.com/api'
const baseUrl = 'https://tutashop.irfanmg.com'
export const store = new Vuex.Store({
  state: {
    token: localStorage.getItem('access_token') || null,
    products: [],
    domainName: baseUrl,
    productDetail: [],
    cart: []
  },
  mutations: {
    setToken(state, token) {
      state.token = token
    },
    destroyToken(state) {
      state.token = null
    },
    setProducts (state, products) {
      state.products = products
    },
    setProductDetail(state, payload) {
      state.productDetail = payload
    },
    pushProductToCart (state, productId) {
      state.cart.push({
          id: productId,
          qty: 1
      })
    },
    incrementItemQty (state, cartItem) {
      cartItem.qty++
     },
    decrementProductStock(state, product) {
      product.stock--
    }
  },
  actions: {
    register(context, data) {
      return new Promise((resolve, reject) => {
        axios.post('/register', {
          name: data.name,
          email: data.email,
          password: data.password
        })
          .then(response => {
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    login(context, payload) {
        return new Promise((resolve, reject) => {
            axios.post('/login', {
              username: payload.username,
              password: payload.password,
            })
              .then(response => {
                const token = response.data.access_token
                console.log(response.data)
                localStorage.setItem('access_token', token)
                context.commit('setToken', token)
                resolve(response)
              })
              .catch(error => {
                console.log(error)
                reject(error)
              })
            })
    },
    logout(context) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
      if (context.getters.loggedIn) {
        return new Promise((resolve, reject) => {
          axios.post('/logout')
            .then(response => {
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              resolve(response)
            })
            .catch(error => {
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              reject(error)
            })
        })
      }
    },
    products(context) {
      axios.get('/products/get')
        .then(response => {
          context.commit('setProducts', response.data.products)
        })
        .catch(error => {
          console.log(error)
        })
    },
    productDetail(context, payload) {
      axios.get('/products/get-detail/' + payload.id)
        .then(response => {
          console.log(response.data)
          context.commit('setProductDetail', response.data)
        })
        .catch(error => {
          console.log(error)
      })
    },
    addProductToCart(context, product) {
      const cartItem = context.state.cart.find(item => item.id === product.id)
      if(!cartItem) {
        context.commit('pushProductToCart', product.id)
      } else {
        context.commit('incrementItemQty', cartItem)
      }
      context.commit('decrementProductStock', product)
    },
  },
  getters: {
    products(state) {
      return state.products
    },
    productDetail(state) {
      return state.productDetail.detail
    },
    carts(state) {
      return state.cart.map(cartItem => {
          const product = state.products.find(product => product.id === cartItem.id)
          return {
              name: product.name,
              price: product.price,
              qty: cartItem.qty,
              foto: product.foto
          }
       })
    },
    cartTotal (state, getters) {
      return getters.carts.reduce((total, product) => total + product.price * product.qty, 0)
    },
  }
})


